import buildLocalizeFn from "./buildLocalizeFn";

const monthValues = {
  wide: [
    "１月",
    "２月",
    "３月",
    "４月",
    "５月",
    "６月",
    "７月",
    "８月",
    "９月",
    "１０月",
    "１１月",
    "１２月"
  ]
};

const shortDays = ["日", "月", "火", "水", "木", "金", "土"];

const dayValues = {
  narrow: shortDays,
  short: shortDays,
  abbreviated: shortDays,
  wide: ["日曜日", "月曜日", "火曜日", "水曜日", "木曜日", "金曜日", "土曜日"]
};

const eraValues = {
  narrow: ["BC", "AD"],
  abbreviated: ["BC", "AD"],
  wide: ["紀元前", "紀元後"]
};

const quarterValues = {
  narrow: ["1", "2", "3", "4"],
  abbreviated: ["Q1", "Q2", "Q3", "Q4"],
  wide: ["1st quarter", "2nd quarter", "3rd quarter", "4th quarter"]
};

const dayPeriods = {
  am: "午前",
  pm: "午後",
  midnight: "深夜",
  noon: "正午",
  morning: "朝",
  afternoon: "昼",
  evening: "夕",
  night: "夜"
};

const dayPeriodValues = {
  narrow: dayPeriods,
  abbreviated: dayPeriods,
  wide: dayPeriods
};

const localize = {
  ordinalNumber: (dirtyNumber, dirtyOptions) => dirtyNumber + "日",
  month: buildLocalizeFn({
    values: monthValues,
    defaultWidth: "wide"
  }),
  day: buildLocalizeFn({
    values: dayValues,
    defaultWidth: "wide"
  }),
  era: buildLocalizeFn({
    values: eraValues,
    defaultWidth: "wide"
  }),
  quarter: buildLocalizeFn({
    values: quarterValues,
    defaultWidth: "wide",
    argumentCallback: quarter => Number(quarter) - 1
  }),
  dayPeriod: buildLocalizeFn({
    values: dayPeriodValues,
    defaultWidth: "wide"
  })
};

export default localize;
